@import url('https://fonts.googleapis.com/css2?family=Lexend&display=swap');

* {
  margin: 0;
  scroll-behavior: smooth;
  font-family: 'Lexend', sans-serif;
}
* ::-webkit-scrollbar {
  height: 8px;
  width: 8px;
}
* ::-webkit-scrollbar-thumb {
  background: #7a8764;
  border-radius: 10px;
  cursor: hand;
}
* ::-webkit-scrollbar-track {
  border-radius: 10px;
  box-shadow: inset 0 0 5px #bbb;
}

